import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome'
import Chat from '@/components/Chat'
Vue.use(Router)

export default new Router({
  mode:"history",
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat,
      props:true  ,// now it expects to have props passed in

      // guarding the entry if name was not entered
      beforeEnter:(to,from,next)=>{
        if(to.params.name || localStorage.name){
          next()
        }else{
          next({name:"Welcome"})
        }
        }
    }

  ]
})



